import React, { useState, useEffect } from "react";
import axios from "axios";
import Notifications from "components/Notification/Notification";
import { product } from "../data/api";
import { FiArrowLeft } from "react-icons/fi";
import empty from "../assets/img/inventory-empty.svg";
import { useAuth } from "contexts/AuthContext";
import { BiBarcodeReader } from "react-icons/bi";

// reactstrap components
import {
  Card,
  CardHeader,
  CardBody,
  CardTitle,
  Table,
  Row,
  Col,
  Button,
  Input,
  FormGroup,
  ButtonGroup,
  Modal,
  ModalBody,
  ModalHeader,
} from "reactstrap";

function Inventory() {
  //let symbol = "₦";
  const lowQuantity = 10;
  const [products, setProducts] = useState([]);
  const [currentProduct, setCurrentProduct] = useState({});
  const [temp, setTemp] = useState([]);
  const [dataload, setDataLoad] = useState(true);
  const { userDetail } = useAuth();

  const [scannerInput, setScannerInput] = useState("");
  const [reason, setReason] = useState("");

  const [selected, setSelected] = useState({});
  const [mode, setMode] = useState("add");
  const [notificationStatus, setNotificationStatus] = useState(false);
  const [notificationDetails, setNotificationDetails] = useState({
    msg: "",
    type: "",
  });

  const [modal, setModal] = useState(false);
  const toggle = () => setModal(!modal);

  useEffect(
    () => {
      async function fetchProducts() {
        let id;
        if (userDetail.type === "staff") {
          id = userDetail.store;
        } else {
          id = userDetail._id;
        }
        await axios
          .get(product.showStoreProducts + "/" + id)
          .then((response) => {
            if (response.data.status === true) {
              setProducts(response.data.data);
              setDataLoad(false);
            } else {
              setNotificationDetails({
                msg: "Error Loading Inventory, Please Referesh The Page",
                type: "danger",
              });
              setNotificationStatus(true);
              setDataLoad(false);
            }
          })
          .catch((error) => {
            if (error.response) {
              setNotificationDetails({
                msg: error.response.data.msg,
                type: "danger",
              });
              setNotificationStatus(true);
            } else {
              setNotificationDetails({ msg: "Network Error!", type: "danger" });
              setNotificationStatus(true);
            }
          });
      }
      fetchProducts();
    },
    // eslint-disable-next-line
    []
  );

  async function updateInventory(type) {
    var new_variation = [];
    let toSend = [];
    let toSendScanned = [];
    async function start() {
      new_variation = JSON.parse(JSON.stringify(temp));
      if (type === "quantity") {
        new_variation.forEach((variationItem) => {
          // console.log(variationItem.quantity + " " + index);
          if (variationItem.quantity !== 0) {
            toSend.push({
              quantity: variationItem.quantity,
              variation: variationItem.variation,
              type: variationItem.type,
              reason,
            });
          }
        });
      } else if (type === "barcode") {
        toSendScanned.push({
          barcodes: scannerInput,
          variation: selected.variation,
          type: selected.type,
          reason,
          mode,
        });
      }

      // console.log(toSend);
      // setCurrentProduct({ ...currentProduct, variations: new_variation });

      return { quantities: toSend, barcodes: toSendScanned };
    }
    start().then((response) => {
      axios
        .patch(product.updateInventory + "/" + currentProduct._id, response)
        .then((res) => {
          if (res.data.status) {
            setNotificationDetails({
              msg:
                mode === "add"
                  ? "Product Restocked Successfully"
                  : "Removed product from list successfully",
              type: "success",
              change: res.data.change,
            });
            setCurrentProduct(res.data.data);
          } else {
            setNotificationDetails({
              msg: "Error Restocking Product",
              type: "danger",
            });
          }
          setNotificationStatus(true);
        })
        .catch((error) => {
          if (error.response) {
            setNotificationDetails({
              msg: error.response.data.msg,
              type: "danger",
            });
            setNotificationStatus(true);
          } else {
            setNotificationDetails({ msg: "Network Error!", type: "danger" });
            setNotificationStatus(true);
          }
        });
    });
  }
  const [loading, setLoading] = useState(true);

  function selectProduct(id, key) {
    async function select() {
      setCurrentProduct(products.filter((item) => item._id === id)[0]);
      return products[key].variations;
    }
    select().then((response) => {
      var new_variation = [];
      new_variation = JSON.parse(JSON.stringify(response));

      new_variation.forEach((tempItem) => {
        tempItem.quantity = 0;
      });
      setTemp(new_variation);
    });

    setLoading(false);
  }

  function changeVariationData(id, value, type) {
    var new_variation = [];
    new_variation = JSON.parse(JSON.stringify(temp));
    switch (type) {
      case "quantity":
        new_variation[id].quantity = Number(value);
        break;
      default:
      // code block
    }
    setTemp(new_variation);
  }

  const [q, setQ] = useState("");
  const [searchColumns, setSearchColumns] = useState(["product_name", "brand"]);

  function search(rows) {
    return rows.filter((row) =>
      searchColumns.some(
        (column) =>
          row[column].toString().toLowerCase().indexOf(q.toLowerCase()) > -1
      )
    );
  }

  const result = products.map(({ _id, product_name, brand, variations }) => ({
    _id,
    product_name,
    brand,
    variations,
  }));
  const columns = result[0] && Object.keys(result[0]);
  return (
    <>
      {notificationStatus ? (
        <Notifications details={notificationDetails} />
      ) : null}
      <div className="content">
        <Row>
          <Col md="12">
            {loading === true ? (
              <Card>
                <CardHeader>
                  <CardTitle className="pull-left" tag="h4">
                    Inventory
                  </CardTitle>
                  <FormGroup style={{ width: "100%" }} className="pull-right">
                    <Input
                      placeholder="Search based on checked items"
                      type="text"
                      value={q}
                      onChange={(e) => setQ(e.target.value)}
                    />
                  </FormGroup>
                </CardHeader>

                <Col sm="12" style={{ overflowX: "auto" }}>
                  <ButtonGroup
                    className="btn-group-toggle float-right"
                    data-toggle="buttons"
                  >
                    {columns &&
                      columns.map((column, key) => (
                        <Button
                          tag="label"
                          className="btn-simple"
                          color="info"
                          id={key}
                          size="sm"
                          onClick={() => {
                            const checked = searchColumns.includes(column);
                            setSearchColumns((prev) =>
                              checked
                                ? prev.filter((sc) => sc !== column)
                                : [...prev, column]
                            );
                          }}
                          active={searchColumns.includes(column)}
                        >
                          {column}
                        </Button>
                      ))}
                  </ButtonGroup>
                </Col>

                {dataload === false ? (
                  <>
                    {products.length > 0 ? (
                      <CardBody>
                        <Table
                          className="tablesorter"
                          responsive
                          style={{ overflow: "unset" }}
                        >
                          <thead className="text-primary">
                            <tr>
                              <th>Product Name</th>
                              <th>Variations - Quantity</th>
                              <th>Action</th>
                            </tr>
                          </thead>
                          <tbody>
                            {search(result).map((productItem, key) => (
                              <tr key={key}>
                                <td>{productItem.product_name}</td>
                                <td>
                                  {productItem.variations.map(
                                    (variation, key) => (
                                      <div key={key}>
                                        {variation.variation}:{" "}
                                        {variation.quantity}
                                        <div
                                          style={{
                                            float: "right",
                                            backgroundColor:
                                              variation.quantity > lowQuantity
                                                ? "green"
                                                : "red",
                                            width: "20px",
                                            height: "15px",
                                          }}
                                        ></div>
                                      </div>
                                    )
                                  )}
                                </td>
                                <td>
                                  <div>
                                    {userDetail.type === "staff" ? null : (
                                      <Button
                                        onClick={() =>
                                          selectProduct(productItem._id, key)
                                        }
                                        className="btn-fill"
                                        size="sm"
                                        color="primary"
                                        type="submit"
                                      >
                                        Show Inventory
                                      </Button>
                                    )}
                                  </div>
                                </td>
                              </tr>
                            ))}
                          </tbody>
                        </Table>
                      </CardBody>
                    ) : (
                      <div style={{ textAlign: "center", padding: "20px" }}>
                        <img
                          src={empty}
                          style={{ marginBottom: "30px" }}
                          height="250px"
                          alt="Nothing to show yet"
                        />
                        <br />
                        <CardTitle tag="h4">
                          {" "}
                          Nothing To Show Yet... Add some products to the system
                          in order to manage inventory
                        </CardTitle>
                      </div>
                    )}
                  </>
                ) : (
                  "Loading"
                )}
              </Card>
            ) : (
              <Button
                style={{ width: "100%", marginBottom: "15px" }}
                onClick={() => setLoading(!loading)}
                className="btn-fill"
                color="primary"
                size="sm"
              >
                <FiArrowLeft size={20} />{" "}
                <font style={{ paddingLeft: "30px" }}>Back To Inventory </font>
              </Button>
            )}
          </Col>{" "}
          {loading === false ? (
            <Col md="12">
              <Row>
                <Col>
                  <Card className="card-user">
                    <CardBody>
                      <div className="card-description">
                        <Table className="tablesorter" responsive>
                          <thead className="text-primary">
                            <tr>
                              <th>Item</th>
                              <th>Detail</th>
                            </tr>
                          </thead>
                          <tbody>
                            <tr>
                              <td>Product Name</td>
                              <td>
                                <h5 className="title">
                                  {" "}
                                  {currentProduct.product_name}{" "}
                                </h5>
                              </td>
                            </tr>
                            <tr>
                              <td>Brand Name</td>
                              <td>
                                <h5 className="title">
                                  {" "}
                                  {currentProduct.brand}{" "}
                                </h5>
                              </td>
                            </tr>
                            <tr>
                              <td>Product Description</td>
                              <td>
                                <h5 className="title">
                                  {" "}
                                  {currentProduct.product_desc}{" "}
                                </h5>
                              </td>
                            </tr>
                          </tbody>
                        </Table>
                        <hr />
                      </div>
                    </CardBody>
                  </Card>
                </Col>
                <Col md="7">
                  <Card className="card-user">
                    <CardBody>
                      <Table className="tablesorter" responsive>
                        <thead className="text-primary">
                          <tr>
                            <th>Variations</th>
                            <th>Quantity</th>
                            <th>Restock</th>
                          </tr>
                        </thead>
                        <tbody>
                          {currentProduct.variations.map(
                            (variationItem, key) => (
                              <tr key={key}>
                                <td>
                                  <Col className="pr-md-1" md="12">
                                    <FormGroup>
                                      Variation : {variationItem.variation}{" "}
                                      <br />
                                      {/* Model: {variationItem.model} <br /> */}
                                      {/* Buying Price:{" "}
                                  {variationItem.buying_price.toLocaleString()}{" "}
                                  <br /> */}
                                      Selling Price:{" "}
                                      {variationItem.selling_price.toLocaleString()}
                                    </FormGroup>
                                  </Col>
                                </td>

                                <td>
                                  <Col md="12">
                                    <label>Quantity</label>
                                    <Input
                                      placeholder=""
                                      type="text"
                                      disabled
                                      value={
                                        variationItem.type === "Type 1"
                                          ? variationItem.barcodes.length
                                          : variationItem.quantity || 0
                                      }
                                    />
                                  </Col>
                                </td>
                                <td>
                                  <Col md="12">
                                    <label>
                                      Addition: {variationItem.type}
                                    </label>
                                    {variationItem.type === "Type 3" ||
                                    variationItem.type === "Type 2" ? (
                                      <Input
                                        placeholder="5"
                                        type="number"
                                        defaultValue={0}
                                        onChange={(e) =>
                                          changeVariationData(
                                            key,
                                            e.target.value,
                                            "quantity"
                                          )
                                        }
                                      />
                                    ) : null}
                                    {variationItem.type === "Type 2" ||
                                    variationItem.type === "Type 1" ? (
                                      <>
                                        <Button
                                          onClick={() => {
                                            toggle();
                                            setSelected(variationItem);
                                          }}
                                          className="btn-fill"
                                          color="primary"
                                          type="submit"
                                          size="sm"
                                          style={{ width: "100%" }}
                                        >
                                          View <BiBarcodeReader />
                                        </Button>
                                      </>
                                    ) : null}
                                  </Col>
                                </td>
                              </tr>
                            )
                          )}
                        </tbody>
                      </Table>
                    </CardBody>
                    <Button
                      onClick={() => updateInventory("quantity")}
                      className={`btn-fill`}
                      style={{
                        marginTop: "-15px",
                      }}
                      color="primary"
                      type="submit"
                    >
                      Update Inventory
                    </Button>
                  </Card>
                </Col>
              </Row>
            </Col>
          ) : null}
        </Row>
      </div>

      <Modal isOpen={modal} isfullscreen={true}>
        <ModalHeader toggle={toggle}>
          Add/View/Remove Mode :{" "}
          <b>
            {currentProduct.product_name} - {selected.variation}
          </b>
        </ModalHeader>
        <ModalBody>
          <div style={{ display: "flex", justifyContent: "space-between" }}>
            <Button
              size={"sm"}
              style={{ color: "white" }}
              disabled={mode === "add"}
              onClick={() => {
                setMode("add");
              }}
            >
              Add Mode
            </Button>
            <Button
              size={"sm"}
              style={{ color: "white" }}
              disabled={mode === "remove"}
              onClick={() => {
                setMode("remove");
              }}
            >
              Remove Mode
            </Button>
            <Button
              size={"sm"}
              style={{ color: "white" }}
              disabled={mode === "view"}
              onClick={() => {
                setMode("view");
              }}
            >
              View Mode
            </Button>
          </div>
          {mode === "add" || mode === "remove" ? (
            <div style={{ textAlign: "center" }}>
              <div>Barcode Scanner Input</div>
              <div>
                {selected.type === "Type 1"
                  ? "Each scanned input will be added to or removed from the list of existing products"
                  : "The former barcode will will overidden/removed as this is a Type 2 varaition."}
              </div>
              <BiBarcodeReader size={200} color="primary" />
              <FormGroup>
                <label>Scanned Input</label>

                <Input
                  placeholder="Barcode Input: E.G 1277392103"
                  type="text"
                  onChange={(e) => setScannerInput(e.target.value)}
                  style={{ color: "unset" }}
                />
              </FormGroup>
              {mode === "remove" ? (
                <FormGroup>
                  <label>Reason</label>
                  <Input
                    placeholder="Reason for item to be removed. E.G Faulty device."
                    type="text"
                    onChange={(e) => setReason(e.target.value)}
                    style={{ color: "unset" }}
                  />
                </FormGroup>
              ) : null}
              <Button
                onClick={() => updateInventory("barcode")}
                className={`${mode === "add" ? "" : "danger"}`}
                style={{
                  marginBottom: "5px",
                  background: `${mode === "add" ? "" : "red"}`,
                }}
                color="primary"
                type="submit"
                size="sm"
              >
                {mode.toLocaleUpperCase()} Barcode - {selected.type}{" "}
                <BiBarcodeReader size={30} />
              </Button>
            </div>
          ) : null}

          {mode === "view" ? (
            <Table>
              <thead>
                <tr>
                  <th style={{ color: "unset" }}>Device Barcode(s)</th>
                </tr>
              </thead>
              <tbody>
                {selected.barcodes.map((bar, key) => (
                  <tr key={key}>
                    <td style={{ color: "black" }}>{bar}</td>
                  </tr>
                ))}
              </tbody>
            </Table>
          ) : null}
        </ModalBody>
      </Modal>
    </>
  );
}

export default Inventory;
